import React, { useState, ChangeEvent, FormEvent } from 'react';
import axios from 'axios';
import styles from '@styles/SignupForm.module.scss';
import { useModal } from '@contexts/ModalContext';
import { useNavigate } from 'react-router-dom';
import { API_ROUTES } from '../../config/apiConfig';

interface UserData {
  name: string;
  email: string;
  password: string;
  role: string;
}

const SignupForm: React.FC = () => {
  const { openModal } = useModal();
  const navigate = useNavigate();

  const [userData, setUserData] = useState<UserData>({
    name: '',
    email: '',
    password: '',
    role: 'USER',
  });

  const [testPass, setTestPass] = useState<string>('');

  const handleChange = (event: ChangeEvent<HTMLInputElement>): void => {
    const { id, value } = event.target;
    setUserData((prevUserData) => ({
      ...prevUserData,
      [id]: value,
    }));
  };

  const namePattern = /^[-a-zA-Z]{2,50}$/;
  const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  const passwordPattern = /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%^&+=!]).{8,40}$/;

  async function handleSubmit(e: FormEvent): Promise<void> {
    e.preventDefault();
    console.log(userData);

    if (!namePattern.test(userData.name)) {
      openModal(
        <>
          <h3> </h3>
          <p> 2  50     .</p>
        </>,
      );
      return;
    }

    if (!emailPattern.test(userData.email)) {
      openModal(
        <>
          <h3> </h3>
          <p>    .</p>
        </>,
      );
      return;
    }

    if (!passwordPattern.test(userData.password)) {
      openModal(
        <>
          <h3> </h3>
          <p> 8  40 , , , ,   .</p>
        </>,
      );
      return;
    }

    if (userData.password !== testPass) {
      openModal(
        <>
          <h3> </h3>
          <p>  .  .</p>
        </>,
      );
      return;
    }

    try {
      await axios.post(API_ROUTES.SIGNUP, userData, { withCredentials: true });
      openModal(
        <>
          <h3> </h3>
          <p> .</p>
          <p>. 3    .</p>
        </>,
      );
      setTimeout(() => {
        navigate('/');
      }, 3000);
    } catch (error) {
      let errorMessage = '   .';
      //        
      if (axios.isAxiosError(error) && error.response) {
        errorMessage = error.response.data.message || errorMessage; //    
      }
      openModal(
        <>
          <h3> </h3>
          <p>{errorMessage}</p>
        </>,
      );
      console.error(error);
    }
  }

  return (
    <form onSubmit={handleSubmit} className={styles.SignupForm}>
      <div className={styles.signupHeader}>
        ICT Project 2024.08 <br />
         
      </div>
      <div className={styles.formControl}>
        <label htmlFor="name"></label>
        <input type="text" id="name" value={userData.name} onChange={handleChange} placeholder=" " />
      </div>
      <div className={styles.formControl}>
        <label htmlFor="email"></label>
        <input id="email" type="text" value={userData.email} onChange={handleChange} placeholder=" " />
      </div>
      <div className={styles.formControl}>
        <label htmlFor="password"></label>
        <input
          id="password"
          type="password"
          value={userData.password}
          onChange={handleChange}
          placeholder=" "
        />
      </div>
      <div className={styles.formControl}>
        <input
          type="password"
          value={testPass}
          onChange={(e: ChangeEvent<HTMLInputElement>) => {
            setTestPass(e.target.value);
          }}
          placeholder=" "
        />
      </div>
      <button className={styles.btnSuccess} type="submit">
         
      </button>
      <button
        className={styles.button}
        onClick={() => {
          navigate('/login');
        }}
      >
         
      </button>
    </form>
  );
};

export default SignupForm;
