import React, { useState, FormEvent } from 'react';
import styles from '@styles/LoginForm.module.scss';
import axios from 'axios';
import { useNavigate } from 'react-router-dom'; // useNavigate     react-router-dom 
import { useModal } from '@contexts/ModalContext'; // useModal    
import { API_ROUTES } from '../../config/apiConfig';

// LoginForm   
const LoginForm: React.FC = () => {
  const [email, setEmail] = useState<string>(''); //    
  const [password, setPassword] = useState<string>(''); //    
  const { openModal } = useModal(); //  
  const navigate = useNavigate(); //  

  //     
  async function handleSubmit(e: FormEvent): Promise<void> {
    //      
    e.preventDefault();

    //   
    if (!email || !password) {
      openModal(
        <>
          <h3> </h3>
          <p>   .</p>
        </>,
      );
      return;
    }

    //  API 
    try {
      await axios.post(API_ROUTES.LOGIN, { email: email, password: password }, { withCredentials: true });
      console.log(' ');
      openModal(
        <>
          <h3> </h3>
        </>,
      );
      navigate('/');
    } catch (error) {
      openModal(
        <>
          <h3> </h3>
          <p>   .</p>
        </>,
      );
      console.error(error);
    }
  }

  return (
    <form onSubmit={handleSubmit} className={styles.LoginForm}>
      <div className={styles.loginHeader}>
        ICT Project 2024.08 <br />
        LOGIN
      </div>
      <div className={styles.formControl}>
        <label htmlFor="email"></label>
        <input
          type="text"
          id="email"
          className={styles.formInput}
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="email"
        />
      </div>
      <div className={styles.formControl}>
        <label htmlFor="password"></label>
        <input
          type="password"
          id="password"
          className={styles.formInput}
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="password"
        />
      </div>
      <button className={styles.btnSuccess} type="submit">
        
      </button>
      <button
        className={styles.button}
        onClick={() => {
          navigate('/signup');
        }}
      >
         
      </button>
    </form>
  );
};

export default LoginForm;
